@media screen and (max-width: 436px){
    .home-page{
        height: auto;
        width: 100%;
        text-align: center
    }
}

@media screen and (min-width: 436px){
    .home-page{
        height: 100vh;
        width: 100%;
        text-align: center
    }
}

.home-page-background{
    filter: grayscale(100%);
    opacity: .1;
    position: absolute;
    left: 0;
}

.home-page-title{
    padding-top: 45px;
    align-content: center;
}

.home-page-slogan{
    font-family: 'Rouge Script', cursive;
    margin-top: 5%;
    margin-bottom: 4%
}

.home-page-ending{
    margin-top: 5%;
    width: 60%;
    margin-left: 20%;
    margin-right: 0%;
}